.kui-modal{position:fixed;top:0;left:0;width:100%;height:100%;visibility:hidden;-webkit-transition:visibility .4s ease;transition:visibility .4s ease}.kui-modal--visible{visibility:visible}.kui-modal__bg{position:absolute;width:100%;height:100%;opacity:0;-webkit-transition:opacity .4s ease;transition:opacity .4s ease}.kui-theme--light .kui-modal__bg{background:rgba(0,0,0,.55)}.kui-theme--dark .kui-modal__bg{background:rgba(19,25,36,.9)}.kui-modal__bg--visible{opacity:1}.kui-modal__content{position:absolute;top:50%;left:50%;max-width:400px;width:100%;opacity:0;-webkit-transform:translate(-50%,-50%) translateY(80px);transform:translate(-50%,-50%) translateY(80px);transition:opacity .4s,-webkit-transform .4s;-webkit-transition:opacity .4s,-webkit-transform .4s;transition:opacity .4s,transform .4s;transition:opacity .4s,transform .4s,-webkit-transform .4s}.kui-theme--light .kui-modal__content{background:#fff}.kui-theme--dark .kui-modal__content{background:#122531}.kui-modal__content--visible{opacity:1;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}.kui-modal__wrapper{display:-webkit-flex;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-align:center;-ms-flex-align:center;align-items:center;width:100%;height:100%;padding:40px;text-align:center}.kui-modal__title{font-size:1.8em;line-height:1.4;width:100%;margin-bottom:40px}.kui-theme--light .kui-modal__title{color:#000}.kui-theme--dark .kui-modal__title{color:#fff}.kui-modal__description{font-size:1.6em;line-height:1.3;letter-spacing:.1px;margin-bottom:40px}.kui-theme--light .kui-modal__description{color:rgba(0,0,0,.55)}.kui-theme--dark .kui-modal__description{color:hsla(0,0%,100%,.55)}.kui-modal .kui-icon{position:absolute;top:10px;right:10px}